.conteudo {
    width: 100%;
    padding-bottom: 30px;
}

@media screen and (min-width: 481px) {
    .conteudo {
        width: 80%;
        padding-bottom: 30px;
    }
}

.centralizado {
    text-align: center;
}

.card-main {
    background-color: #fff;
    -webkit-box-shadow: 0px 2px 8px -2px rgba(140,140,140,1);
    -moz-box-shadow: 0px 2px 8px -2px rgba(140,140,140,1);
    box-shadow: 0px 2px 8px -2px rgba(140,140,140,1);
    max-height: 3000px;
    width: 100%;
}

.centralizar {
    display: flex;
    justify-content: center;
}